
.hero {
    height: 100vh;
    position: relative;
    /* box-shadow: 0px 5px 50px rgba(0, 196, 204, 0.4); */
    padding-top: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-image: linear-gradient(rgba(0, 196, 204, 0.1) 70%, rgba(87, 163, 189, 1)), url(../assets/images/ngome_landing_page_site.jpg);
    background-size: cover;
    background-position: center;
}

button {
    position: relative;
    z-index: 10;
}

.hero-wrapper{
    /* height: 100%; */
    width: 80%;
    
    color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
}

.hero-subtitle {
    font-size: 2.5rem;
    font-family: BebasNeue;
    font-weight: 400;
    text-shadow: 0 0 0.75em rgb(51, 51, 51);
    margin: 10px 10px 0;
}

.hero-title {
    position: relative;
    width: fit-content;
    font-size: 7rem;
    font-family: BebasNeue;
    font-weight: 400;
    text-shadow: 0 0 0.75em rgb(51, 51, 51);
    margin: 0 10px 10px;
}

.hero-title::after {
    content: "";
    height: 3px;
    width: 110%;
    background-color: white;  
    position: absolute;  
    left: 0;
    bottom: 0;
}

.hero-description {
    font-family: Montserrat;
    font-weight: 200;
    font-size: 1.25rem;
    text-shadow: 0 0 0.75em rgb(51, 51, 51);
    padding: 0 70px 0 10px;
}